import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import VueAxios from 'vue-axios'

Vue.use(Vuex);
Vue.use(VueAxios, axios);

axios.defaults.baseURL = '/holiday';
axios.defaults.headers.get['Content-Type'] = 'application/json';


export const store = new Vuex.Store({

    state: {
      searchResult: [],
      errorMessage: '',
      loadingState: false,
      selectedDepartureAirport: '',
      selectedArrivalAirport: '',
      availableAirports: ['','NewYork', 'SanFrancisco','London','Frankfurt','Amsterdam','Paris', 'Dubai']
    },
    getters: {
      getAailableAirports(state) {
        return state.availableAirports;
      }
    },
    mutations: {
      setSearchResult(state, payload) {
        state.searchResult = payload;
      },
      setError(state, payload) {
        state.errorMessage = payload;
      },
      enableLoadingState (state) {
        state.loadingState = true;
      },
      disableLoadingState(state) {
        state.loadingState = false;
      },
      changeDeparture(state, payload) {
        state.selectedDepartureAirport = payload;
      },
      changeArrival(state, payload) {
        state.selectedArrivalAirport = payload;
      },
      shuffle(state) {
        if (state.loadingState) {
          return;
        }
        const { availableAirports } = state;
        const len = availableAirports.length;
        const randomDeparture = Math.floor(Math.random() * Math.floor(len-1)) +1;
        const randomArrival = Math.floor(Math.random() * Math.floor(len-1)) +1;
        state.selectedDepartureAirport = availableAirports[randomDeparture];
        state.selectedArrivalAirport = availableAirports[randomArrival];
      }
    },
    actions: {
      triggerSearch (context, payload) {
        console.log(context);
        console.log(payload);
        context.commit('setSearchResult',[]);
        const {departure, arrival} = payload;
        if (departure === '') {
          context.commit('setError','Please select your departure airport!'); 
          return;   
        }

        if (arrival === '') {
          context.commit('setError','Please select your arrival airport!'); 
          return;   
        }

        if (departure === arrival) {
          context.commit('setError','Departure and arrival are equal. Take the bus!'); 
          return;   
        }

        
        context.commit('setError','');
        context.commit('enableLoadingState');


      

        axios.post('/search',
        {
          "flightDate": "2020-06-20",
          "departureAirport": departure ? departure.toUpperCase() : '',
          "arrivalAirport": arrival ? arrival.toUpperCase() : '',
        })
        .then((response) => {
          context.commit('disableLoadingState');
          context.commit('setSearchResult', response.data);
        }).catch((error) => {
          context.commit('disableLoadingState');
          console.log(error);
          context.commit('setError', error);
        });
        ;
      }
    }
  });
