<template>
    <div class="panel ">
        <div class="container">
            <H2>Pick your next flight!</H2>
        </div>    
        <div class="container">
            <label>From</label>
            <select type="select" :disabled="loadingState" v-model="selectedDepartureAirport" @change="changeDep">
                 <option v-for="option in getAailableAirports()" :value="option" :key="option">
                        {{option}}
                </option>
            </select>
            <label>To</label>
            <select type="select"  :disabled="loadingState" v-model="selectedArrivalAirport" @change="changeArr">
                <option v-for="option in getAailableAirports()" :value="option" :key="option">
                        {{option}}
                </option>
            </select>
            <label>At</label>
             <select type="select" :disabled="loadingState">
                <option>
                    Today
                </option>
            </select>
            <button class="search-button" @click="triggerSearch({
                departure: selectedDepartureAirport, 
                arrival: selectedArrivalAirport
                })" :disabled="loadingState">SEARCH</button>
        </div>
         <div class="container">
                <div class="random">
                    <p  href="#">Or let us choose one for you <a :disabled="loadingState" href="#" @click.prevent="shuffle" class="random-link">randomly</a></p> 
                </div>
            </div>
    </div>

</template>
<script>
import {mapActions} from 'vuex';
import {mapGetters} from 'vuex';
import {mapMutations} from 'vuex';

export default {
    methods: {
        ...mapActions([
            'triggerSearch'
        ]),
        ...mapGetters([
            'getAailableAirports'    
        ]),
        ...mapMutations([
            'changeDeparture',
            'changeArrival',
            'shuffle'
        ]),
        changeDep(event) {
            this.changeDeparture(event.target.value);
        },
        changeArr(event) {
            this.changeArrival(event.target.value);
        }
    },
    computed: {
        loadingState() {
            return this.$store.state.loadingState;
        },
        selectedDepartureAirport() {
            return this.$store.state.selectedDepartureAirport;
        },
        selectedArrivalAirport() {
            return this.$store.state.selectedArrivalAirport;
        }
    }
}
</script>
